import { toast } from "react-toastify";

export const RegisterValidate = (
  fullName,
  email,
  username,
  password,
  code,
  whereNow,
  tinh,
  check
) => {
  if (!fullName) {
    toast.error("Vui lng nhp h v tn");
    return false;
  }

  if (!email) {
    toast.error("Vui lng nhp email");
    return false;
  }

  let reg = /\S+@\S+\.\S+/;
  if (!reg.test(email)) {
    toast.error("Email bn nhp khng ng nh dng");
    return false;
  }

  if (!username) {
    toast.error("Vui lng nhp tn ngi dng");
    return false;
  }

  if (/\s/.test(username)) {
    toast.error("Tn ngi dng khng c cha khong trng");
    return false;
  }

  if (!password) {
    toast.error("Vui lng nhp mt khu");
    return false;
  }

  const passwordRegex =
    /^(?=.*[A-Z])(?=.*[!@#$%^&*(),.?":{}|<>])(?=.*\d)(?=.*[a-z]).{8,}$/;
  if (!passwordRegex.test(password)) {
    toast.error(
      "Mt khu phi cha t nht 8 k t, bao gm ch hoa, ch thng, s v k t c bit"
    );
    return false;
  }

  if (!code) {
    toast.error("M xc thc khng c b trng");
    return false;
  }

  if (!whereNow) {
    toast.error("Vui lng cho chng ti bit bn bit chng ti qua u?");
    return false;
  }

  if (!tinh) {
    toast.error("Vui lng nhp tnh bn ang ");
    return;
  }

  if (!check) {
    toast.error("Bn vui lng ng  vi iu khon s dng");
    return;
  }

  return true;
};
