import { toast } from "react-toastify";

export const LoginValidate = (username, password) => {
  if (!username) {
    toast.error("Vui lng nhp tn ngi dng");
    return false;
  }

  if (/\s/.test(username)) {
    toast.error("Tn ngi dng khng c cha khong trng");
    return false;
  }

  if (!password) {
    toast.error("Vui lng nhp mt khu");
    return false;
  }

  const passwordRegex =
    /^(?=.*[A-Z])(?=.*[!@#$%^&*(),.?":{}|<>])(?=.*\d)(?=.*[a-z]).{8,}$/;
  if (!passwordRegex.test(password)) {
    toast.error(
      "Mt khu phi cha t nht 8 k t, bao gm ch hoa, ch thng, s v k t c bit"
    );
    return false;
  }

  return true;
};
