import { toast } from "react-toastify";

export const ForgotValidate = (email, code, password) => {
  if (!email) {
    toast.error("Vui lng nhp email");
    return false;
  }

  let reg = /\S+@\S+\.\S+/;
  if (!reg.test(email)) {
    toast.error("Email bn nhp khng ng nh dng");
    return false;
  }

  if (!code) {
    toast.error("M xc thc khng c b trng");
    return false;
  }

  if (!password) {
    toast.error("Vui lng nhp mt khu");
    return false;
  }

  const passwordRegex =
    /^(?=.*[A-Z])(?=.*[!@#$%^&*(),.?":{}|<>])(?=.*\d)(?=.*[a-z]).{8,}$/;
  if (!passwordRegex.test(password)) {
    toast.error(
      "Mt khu phi cha t nht 8 k t, bao gm ch hoa, ch thng, s v k t c bit"
    );
    return false;
  }

  return true;
};
