import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import "../News/News.scss";
import BootstrapPagination from "../../Service/Pagination";
import logo from "../../../assets/logo.png";
import { Helmet, HelmetProvider } from "react-helmet-async";
import {
  BlogDelete,
  BlogDuyet,
  BlogRead,
  BlogTuchoi,
} from "../../../services/BlogAdminServer";

function News(props) {
  const navigate = useNavigate();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const handleBack = () => {
    navigate(-1);
  };

  const handleCreate = () => {
    navigate("/admin/blog/create");
  };

  const [blog, setBlog] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage] = useState(20);
  const [totalItems, setTotalItems] = useState(0);

  useEffect(() => {
    const blogData = async () => {
      const data = await BlogRead();
      if (data && data.EC === 0) {
        setBlog(data.DT);
        setTotalItems(data.DT.length);
      }
    };
    blogData();
  }, []);

  const handleRefresh = async () => {
    const data = await BlogRead();
    if (data && data.EC === 0) {
      setBlog(data.DT);
      setTotalItems(data.DT.length);
    }
  };

  const [showDropdown, setShowDropdown] = useState(null);

  const handleAction = (id) => {
    setShowDropdown((prev) => (prev === id ? null : id));
  };

  const handleEdit = (id) => {
    navigate(`/admin/blog/edit/${id}`);
  };

  const handleShow = (id) => {
    navigate(`/admin/blog/show/${id}`);
  };

  const handleDelete = async (id) => {
    const isCheck = window.confirm("Bn c chc chn mun xo blog ny khng?");
    if (isCheck) {
      const data = await BlogDelete(id);
      if (data && data.EC === 0) {
        handleRefresh();
        toast.success(data.EM);
      } else {
        toast.error(data.EM);
      }
    } else {
      setShowDropdown(false);
    }
  };

  const [showModel, setShowModel] = useState(false);
  const [idDuyet, setIdDuyet] = useState();

  const handleShowModel = (id) => {
    setShowModel(!showModel);
    setShowDropdown(!showDropdown);
    setIdDuyet(id);
  };

  const handleDuyet = async () => {
    const data = await BlogDuyet(idDuyet);

    if (data && data.EC === 0) {
      toast.success(data.EM);
      handleRefresh();
    } else {
      toast.error(data.EM);
    }
  };
  const handleTuchoi = async () => {
    const data = await BlogTuchoi(idDuyet);

    if (data && data.EC === 0) {
      toast.success(data.EM);
      handleRefresh();
    } else {
      toast.error(data.EM);
    }
  };

  const startIndex = (currentPage - 1) * itemsPerPage;
  const currentBlog = blog.slice(startIndex, startIndex + itemsPerPage);

  const handlePageChange = (page) => {
    setCurrentPage(page);
    window.scrollTo(0, 0);
  };

  return (
    <>
      <HelmetProvider>
        <Helmet>
          <title>CNcode | Tin tc</title>
          <meta
            name="description"
            content="Nn tng hc cng ngh thng tin online"
          />
          <link rel="canonical" href={logo} />
          <link rel="icon" href="uploads/img/18-01-2025/g354ky1ob557wmdz6sca" />
        </Helmet>
      </HelmetProvider>

      {showModel && (
        <div className="model" onClick={handleShowModel}>
          <div className="model-item">
            <h3>Duyt bi vit do ngi dng ng</h3>
            <p>
              Vui lng xem k li bi vit c:
              <br />- Sai li chnh t
              <br />- Quy phm tiu chun ca cng ng
              <br />- Bi vit khng c gi tr hoc khng ng ch 
            </p>
            <div className="row">
              <div className="btn btn-primary" onClick={handleDuyet}>
                ng 
              </div>
              <div className="btn btn-danger" onClick={handleTuchoi}>
                T chi
              </div>
            </div>
          </div>
        </div>
      )}

      <div className="admin">
        <div className="admin-news">
          <div className="btn btn-primary" onClick={handleBack}>
            <i className="fa-solid fa-arrow-left"></i>
            Tr v
          </div>
          <div className="btn btn-primary" onClick={handleRefresh}>
            <i className="fa-solid fa-arrows-rotate"></i>
            Ti li
          </div>
          <div className="btn btn-primary" onClick={handleCreate}>
            <i className="fa-solid fa-plus"></i>
            Thm mi
          </div>
        </div>

        <div className="admin-content">
          <div className="admin-content-item">
            <p className="id">Id</p>
            <p className="title">Tiu </p>
            <p className="right">Tc gi</p>
            <p className="actives">Trng thi</p>
            <p className="show">Hin th</p>
            <p className="action"></p>
          </div>
          {currentBlog && currentBlog.length > 0 ? (
            currentBlog.map((item) => (
              <div className="admin-content-item" key={item._id}>
                <p className="id">{item._id}</p>
                <p className="title">{item.title}</p>
                <p className="right">{item?.authorId?.fullName}</p>
                <p className="actives">
                  {item.isChecked ? "Pht hnh" : "Bn nhp"}
                </p>
                <p className="show">
                  {item.show ? "Cng khai" : "Khng cng khai"}
                </p>
                <p className="action">
                  <i
                    className="btn btn-primary fa-solid fa-ellipsis-vertical"
                    onClick={() => handleAction(item._id)}
                  ></i>
                  {showDropdown === item._id && (
                    <div className="dropdown">
                      <div
                        className="dropdown-links"
                        onClick={() => handleShow(item._id)}
                      >
                        <i className="fa-solid fa-eye"></i>
                        <div className="text">Xem trc</div>
                      </div>
                      <div
                        className="dropdown-links"
                        onClick={() => handleEdit(item._id)}
                      >
                        <i className="fa-solid fa-pen-to-square"></i>
                        <div className="text">Chnh sa</div>
                      </div>
                      <div
                        className="dropdown-links"
                        onClick={() => handleDelete(item._id)}
                      >
                        <i className="fa-solid fa-delete-left"></i>
                        <div className="text">Xo</div>
                      </div>
                      {item.active === "" && (
                        <div
                          className="dropdown-links"
                          style={{ color: "var(--xanh-login)" }}
                          onClick={() => handleShowModel(item._id)}
                        >
                          <i
                            className="fa-solid fa-circle-check"
                            style={{ color: "var(--xanh-login)" }}
                          ></i>
                          <div className="text">Trng thi</div>
                        </div>
                      )}
                    </div>
                  )}
                </p>
              </div>
            ))
          ) : (
            <div>Khng c bi vit no!</div>
          )}
        </div>
        <div className="paginate mt-3">
          <BootstrapPagination
            totalItems={totalItems}
            itemsPerPage={itemsPerPage}
            currentPage={currentPage}
            onPageChange={handlePageChange}
          />
        </div>
      </div>
    </>
  );
}

export default News;
