import { toast } from "react-toastify";

export const BlogCreateValidate = (title, content, description, img, id) => {
  if (!title) {
    toast.error("Vui lng nhp tiu ");
    return false;
  }

  if (!description) {
    toast.error("Vui lng nhp m t cho bi vit");
    return false;
  }

  if (!content) {
    toast.error("Vui lng nhp ni dung bi vit");
    return false;
  }

  if (!img) {
    toast.error("Bn cn upload 1 nh  lm nh ba");
    return false;
  }

  if (!id) {
    toast.error("Bn cn ng nhp  c th ng blog");
    return false;
  }

  return true;
};
