import React, { useEffect } from "react";
import "./Settings.scss";
import { NavLink } from "react-router-dom";
import { HelmetProvider, Helmet } from "react-helmet-async";
import logo from "../../../assets/logo.png";

function Settings(props) {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="admin">
      <HelmetProvider>
        <Helmet>
          <title>CNcode | Ci t chung</title>
          <meta
            name="description"
            content="Nn tng hc cng ngh thng tin online"
          />
          <link rel="canonical" href="https://cncode.vercel.app" />
          <link rel="icon" href={logo} />
        </Helmet>
      </HelmetProvider>

      <div className="tabs">
        <div className="tab">
          <h3>Thng tin gii thiu</h3>
          <NavLink to="/admin/settings/infor">
            <i className="fa-solid fa-list-check"></i>
          </NavLink>
        </div>

        <div className="tab">
          <h3>Phn quyn qun tr</h3>
          <NavLink to="/admin/settings/role">
            <i className="fa-solid fa-list-check"></i>
          </NavLink>
        </div>

        <div className="tab">
          <h3>Bch ho vt phm</h3>
          <NavLink to="/admin/settings/shop">
            <i className="fa-solid fa-list-check"></i>
          </NavLink>
        </div>

        <div className="tab">
          <h3>Khu vn trn my</h3>
          <NavLink to="/admin/settings/opinion">
            <i className="fa-solid fa-list-check"></i>
          </NavLink>
        </div>

        <div className="tab">
          <h3>Gp  ca ngi dng</h3>
          <NavLink to="/admin/settings/opinion">
            <i className="fa-solid fa-list-check"></i>
          </NavLink>
        </div>

        <div className="tab">
          <h3>Tng qu ngi dng</h3>
          <NavLink to="/admin/settings/gift">
            <i className="fa-solid fa-list-check"></i>
          </NavLink>
        </div>

        <div className="tab">
          <h3>Chnh sch bo mt</h3>
          <NavLink to="/admin/settings/ssl">
            <i className="fa-solid fa-list-check"></i>
          </NavLink>
        </div>

        <div className="tab">
          <h3>iu khon s dng</h3>
          <NavLink to="/admin/settings/use">
            <i className="fa-solid fa-list-check"></i>
          </NavLink>
        </div>

        <div className="tab">
          <h3>Chnh sch thnh vin</h3>
          <NavLink to="/admin/settings/members">
            <i className="fa-solid fa-list-check"></i>
          </NavLink>
        </div>

        <div className="tab">
          <h3>Ci t website</h3>
          <NavLink to="/admin/settings/website">
            <i className="fa-solid fa-list-check"></i>
          </NavLink>
        </div>
      </div>
    </div>
  );
}

export default Settings;
