import React from "react";
import { Link } from "react-router-dom";
import HandleLogin from "../../../middlewares/LoginMiddleware";
import "./Login.scss";

function Login({ toggleLogin }) {
  const {
    fullName,
    email,
    whereNow,
    username,
    password,
    code,
    countdown,
    tinh,
    setTinh,
    setFullName,
    setEmail,
    setWhereNow,
    setCode,
    setUsername,
    setPassword,
    showPassword,
    togglePasswordVisibility,
    LoginMiddleware,
    handleRegister,
    handleSendCode,
    isLoading,
    show,
    setShow,
    check,
    setCheck,
    handleForgot,
  } = HandleLogin(toggleLogin);

  return (
    <div className="login">
      <div className="login-back" onClick={toggleLogin}>
        <i className="fa-solid fa-xmark"></i>
      </div>
      <div className="login-main">
        <header>
          <p
            className={show === "login" ? "active" : ""}
            onClick={() => setShow("login")}
          >
            ng nhp
          </p>
          <p
            className={show === "register" ? "active" : ""}
            onClick={() => setShow("register")}
          >
            ng k
          </p>
          <p
            className={show === "forgot" ? "active" : ""}
            onClick={() => setShow("forgot")}
          >
            Qun mt khu
          </p>
        </header>

        {show === "login" && (
          <div className="dangnhap">
            <h5>ng nhp bng ti khon</h5>
            <div className="form-login">
              <input
                className="form-input"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                placeholder="Tn ng nhp"
                autoComplete="username"
                onKeyDown={(e) => {
                  if (e.key === "Enter") {
                    LoginMiddleware();
                  }
                }}
              ></input>
              <div className="form-input-password">
                <input
                  type={showPassword ? "text" : "password"}
                  className="form-input"
                  placeholder="Mt khu"
                  autoComplete="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  onKeyDown={(e) => {
                    if (e.key === "Enter") {
                      LoginMiddleware();
                    }
                  }}
                ></input>
                <div className="form-button">
                  <i
                    onClick={togglePasswordVisibility}
                    className={`fa-solid ${
                      showPassword ? "fa-eye" : "fa-eye-slash"
                    }`}
                  ></i>
                </div>
              </div>
              <div className="form-input-check">
                <input type="checkbox"></input>
                <span>Ghi nh ti cho ln ng nhp k tip</span>
              </div>
              <div className="form-button-submit" onClick={LoginMiddleware}>
                {isLoading ? (
                  <span>
                    <i className="fa-solid fa-spinner fa-spin"></i> ang x
                    l...
                  </span>
                ) : (
                  "ng nhp"
                )}
              </div>
            </div>
          </div>
        )}

        {show === "register" && (
          <div className="dangnhap">
            <h5>ng k ti khon mi</h5>
            <div className="form-login">
              <select
                className="form-input-select"
                value={whereNow}
                onChange={(e) => setWhereNow(e.target.value)}
              >
                <option value="" disabled style={{ color: "#999" }}>
                  Bn bit chng ti qua u?
                </option>
                <option value="Facebook">Facebook</option>
                <option value="Google">Google</option>
                <option value="Youtube">Youtube</option>
                <option value="Zalo">Zalo</option>
                <option value="info">Bn b gii thiu</option>
              </select>
              <input
                className="form-input"
                placeholder="H v tn"
                value={fullName}
                onChange={(e) => setFullName(e.target.value)}
                onKeyDown={(e) => {
                  if (e.key === "Enter") {
                    handleRegister();
                  }
                }}
              ></input>
              <input
                className="form-input"
                placeholder="Email ca bn"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                onKeyDown={(e) => {
                  if (e.key === "Enter") {
                    handleRegister();
                  }
                }}
              ></input>
              <input
                className="form-input"
                placeholder="Tn ng nhp"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                onKeyDown={(e) => {
                  if (e.key === "Enter") {
                    handleRegister();
                  }
                }}
              ></input>
              <div className="form-input-password">
                <input
                  type={showPassword ? "text" : "password"}
                  className="form-input"
                  placeholder="Mt khu"
                  autoComplete="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  onKeyDown={(e) => {
                    if (e.key === "Enter") {
                      handleRegister();
                    }
                  }}
                ></input>
                <div className="form-button">
                  <i
                    onClick={togglePasswordVisibility}
                    className={`fa-solid ${
                      showPassword ? "fa-eye" : "fa-eye-slash"
                    }`}
                  ></i>
                </div>
              </div>
              <div className="form-input-code">
                <input
                  className="form-input"
                  placeholder="M xc thc"
                  value={code}
                  onChange={(e) => setCode(e.target.value)}
                ></input>
                <div
                  className="form-button"
                  style={{ fontSize: "15px" }}
                  onClick={handleSendCode}
                >
                  {countdown !== 0 ? `${countdown} giy...` : "Gi m"}
                </div>
              </div>
              <input
                className="form-input"
                placeholder="Bn ang  tnh no"
                value={tinh}
                onChange={(e) => setTinh(e.target.value)}
              ></input>
              <div className="form-input-check">
                <input
                  checked={check}
                  onChange={(e) => setCheck(e.target.checked)}
                  type="checkbox"
                ></input>
                <span>
                  Ti ng  vi tt c{" "}
                  <Link to="/use" onClick={toggleLogin}>
                    iu khon
                  </Link>{" "}
                  ca CNcode
                </span>
              </div>
              <div className="form-button-submit" onClick={handleRegister}>
                {isLoading ? (
                  <span>
                    <i className="fa-solid fa-spinner fa-spin"></i> ang x
                    l...
                  </span>
                ) : (
                  "ng k"
                )}
              </div>
            </div>
          </div>
        )}

        {show === "forgot" && (
          <div className="dangnhap">
            <h5>Qun mt khu?</h5>
            <div className="form-login">
              <input
                className="form-input"
                placeholder="Email ca bn"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                onKeyDown={(e) => {
                  if (e.key === "Enter") {
                    handleRegister();
                  }
                }}
              ></input>
              <input
                className="form-input"
                placeholder="Tn ng nhp"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                onKeyDown={(e) => {
                  if (e.key === "Enter") {
                    handleRegister();
                  }
                }}
              ></input>
              <div className="form-input-password">
                <input
                  type={showPassword ? "text" : "password"}
                  className="form-input"
                  placeholder="Mt khu mi"
                  autoComplete="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  onKeyDown={(e) => {
                    if (e.key === "Enter") {
                      handleRegister();
                    }
                  }}
                ></input>
                <div className="form-button">
                  <i
                    onClick={togglePasswordVisibility}
                    className={`fa-solid ${
                      showPassword ? "fa-eye" : "fa-eye-slash"
                    }`}
                  ></i>
                </div>
              </div>
              <div className="form-input-code">
                <input
                  className="form-input"
                  placeholder="M xc thc"
                  value={code}
                  onChange={(e) => setCode(e.target.value)}
                ></input>
                <div
                  className="form-button"
                  style={{ fontSize: "15px" }}
                  onClick={handleSendCode}
                >
                  {countdown !== 0 ? `${countdown} giy...` : "Gi m"}
                </div>
              </div>
              <div className="form-button-submit" onClick={handleForgot}>
                {isLoading ? (
                  <span>
                    <i className="fa-solid fa-spinner fa-spin"></i> ang x
                    l...
                  </span>
                ) : (
                  "Ly li mt khu"
                )}
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default Login;
