import React from "react";
import { useSelector } from "react-redux";
import Dropdown from "react-bootstrap/Dropdown";
import { NavLink } from "react-router-dom";
import HandleLogout from "../../../middlewares/LogoutMiddleware";

function UseDropdown() {
  const fullName = useSelector((state) => state.user.account.fullName);
  const username = useSelector((state) => state.user.account.username);
  const role = useSelector((state) => state.user.account.role);

  const { Logout } = HandleLogout();

  return (
    <Dropdown>
      <Dropdown.Toggle as="div" className="custom-dropdown-toggle">
        <div className="greeting">Xin cho</div>
        <div className="user-name">{fullName}</div>
      </Dropdown.Toggle>

      <Dropdown.Menu>
        <Dropdown.Item>
          <NavLink className="dropdown-link" to={`/p/${username}`}>
            Trang c nhn
          </NavLink>
        </Dropdown.Item>
        {role === "admin" && (
          <Dropdown.Item>
            <NavLink className="dropdown-link" to="/admin/dashboard">
              Trang qun tr
            </NavLink>
          </Dropdown.Item>
        )}
        {role === "teacher" && (
          <Dropdown.Item>
            <NavLink className="dropdown-link" to="/quanly">
              Trang qun l
            </NavLink>
          </Dropdown.Item>
        )}
        <Dropdown.Divider />
        <Dropdown.Item>
          <NavLink className="dropdown-link" to="/me/khoahoc">
            Kho hc ca ti
          </NavLink>
        </Dropdown.Item>
        <Dropdown.Item>
          <NavLink className="dropdown-link" to="/me/transaction">
            Qun l giao dch
          </NavLink>
        </Dropdown.Item>
        <Dropdown.Item>
          <NavLink className="dropdown-link" to="/me/khuvuon">
            Khu vn tin hc
          </NavLink>
        </Dropdown.Item>
        <Dropdown.Divider />
        <Dropdown.Item>
          <NavLink className="dropdown-link" to="/me/post">
            Vit Blog
          </NavLink>
        </Dropdown.Item>
        <Dropdown.Item>
          <NavLink className="dropdown-link" to="/me/blog">
            Blog ca ti
          </NavLink>
        </Dropdown.Item>
        <Dropdown.Item>
          <NavLink className="dropdown-link" to="/me/loveblog">
            Blog yu thch
          </NavLink>
        </Dropdown.Item>
        <Dropdown.Divider />
        <Dropdown.Item>
          <NavLink className="dropdown-link" to="/me/settings">
            Ci t
          </NavLink>
        </Dropdown.Item>
        <Dropdown.Item>
          <NavLink className="dropdown-link-logout" onClick={Logout}>
            ng xut
          </NavLink>
        </Dropdown.Item>
      </Dropdown.Menu>
    </Dropdown>
  );
}

export default UseDropdown;
